.card{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.card .img_cont{
    margin-top:30px;
}
.card img{
    width: 25vw;
    height:25vw;
    border: 1px solid black;
    border-radius:10px;

}

.button_container{
    display: flex;
    justify-content: space-around;
    width: 25vw;
    
    margin-top:20px;
}


.button_container button{
    width:6vw;
    height:6vw;
    background-color: white;
    outline:none;
    border: none;
}

button:hover{
    cursor: pointer;
}

.button_container button img{
    width:4vw;
    height:4vw;
}

    
    

