.game_cont{
    position: relative;
    top:20px;
    left:0px;
    width: 100%;
    height:80vh;
    box-sizing: border-box;
    padding: 60px;
  
    

}

table{
    
    width: 100%;
    border-collapse: collapse;
}

table td{
    text-align:  center;
}

table tr:nth-child(1) td:nth-of-type(odd){
    display: flex;
    justify-content: center;
}

table tr:nth-child(1) td:nth-of-type(odd) div{
    height: 40px;
    width:80%;
    vertical-align: bottom;
    padding: 8px;
    font-size: 30px;
    background-color: #fff9c4;
    display: flex;
    align-items:center;
    justify-content: center;

}

.winner{
    font-size: 30px;
    color: #4527a0;
    font-weight: bold;
}

#start_over{
    height:40px;
    width:120px;

    background-color: #4527a0;
    color:white;
    border-radius: 10px;
    outline: none;
    border: none;
    margin-top:30px;
}

#start_over:hover{
    
    background-color: #160646;

}